@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Raleway&family=Roboto:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navbar{background-color:white;width:100%;height:70px;color:black;font-family:'Raleway';font-size:18px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.Navbar .links{width:500px;height:100%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.Navbar .name{margin-left:20px;font-family:'Pacifico';font-size:26px;color:#ce8f1b}.Navbar .link:hover{background-color:white;color:black;height:100%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;cursor:pointer}

.About{display:flex;flex-direction:row;justify-content:center;align-items:center;width:100%}.AboutSmall{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px}.About__photo{width:300px;height:300px;display:flex;flex-direction:column;justify-content:center;margin-right:100px}.AboutSmall__photo{width:100px;height:100px}.About__info{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:500px;height:400px;text-align:left;font-family:'Raleway'}.AboutSmall__info{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;text-align:left;font-family:'Raleway';margin-top:30px}img.photo__small{object-fit:cover;border-radius:50%;width:200px;height:200px}img.photo{object-fit:cover;border-radius:50%;width:300px;height:300px;margin-right:30px}

.Header{background-color:#4b4b4bab;color:#fcfcfc;font-family:'Raleway', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;border-top:solid 1px #ce8f1b;border-bottom:solid 1px #ce8f1b}.Header__small{background-color:#4b4b4bab;color:#fcfcfc;font-family:'Raleway', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;border-top:solid 1px #ce8f1b;border-bottom:solid 1px #ce8f1b}.Header__particles{height:300px;width:100%}.Header__maintext{position:absolute;top:'200px'}.Header__tweeter{color:black;background-color:rgba(219,214,214,0.904);font-family:'Raleway', serif;width:100%;height:300px;display:flex;flex-direction:row;justify-content:space-around;align-items:center;font-size:20px;border-top:solid 1px black;border-bottom:solid 1px black}.HeaderSmall__tweeter{color:black;background-color:rgba(219,214,214,0.904);font-family:'Raleway', serif;width:100%;height:300px;display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:20px;border-top:solid 1px black;border-bottom:solid 1px black;padding-top:20px}.Header__demo{width:380px;height:250px;border:solid 1px black}.HeaderSmall__demo{width:220px;height:130px;border:solid 1px black;margin-left:10px}.Header__info{display:flex;flex-direction:column;align-items:flex-start;width:500px;text-align:left;font-size:16px;margin-left:20px}.HeaderSmall__info{display:flex;flex-direction:column;align-items:flex-start;text-align:left;font-size:13px;margin-left:20px;padding:17px}

.Body{display:flex;flex-direction:column}

.Portfolio{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;align-content:flex-start;width:100%;background-color:white}.Portfolio .Portfolio__block{width:250px;height:250px;-webkit-filter:blur(1px) grayscale(90%);filter:blur(1px) grayscale(90%);opacity:0.8;transition:0.3s;border:solid 0.5px #fff}.Portfolio .Portfolio__block:hover{-webkit-filter:none;filter:none;opacity:1;cursor:pointer}.Portfolio .Portfolio__small{width:120px;height:120px;-webkit-filter:blur(1px) grayscale(90%);filter:blur(1px) grayscale(90%);opacity:0.8;transition:0.3s}.Portfolio .Portfolio__small:hover{-webkit-filter:none;filter:none;opacity:1;cursor:pointer}

.App{display:flex;flex-direction:column;text-align:center;font-size:16px}

